import { getHttp } from "@/kernel/api/http";
import { ApiRequestListOptionsType, ApiResponseType } from "@/kernel/api/types";
import { NotRequestException } from "@/kernel/lib/exceptions/not-request.exception";
import {
  EventGetData,
  EventAddData,
  EventEditData,
  EventSetData,
} from "@/kernel/api/dto/Event";

export const getEventGet = async (
  eventId: number
): Promise<ApiResponseType<EventGetData>> => {
  const res = await getHttp().get<ApiResponseType<EventGetData> | false>(
    "/event/get",
    {
      searchParams: {
        event_id: eventId,
      },
    }
  );

  if (!res) {
    throw new NotRequestException(getEventGet.name);
  }

  return res;
};

export const getEventDelete = async (
  eventId: number
): Promise<ApiResponseType> => {
  const res = await getHttp().get<ApiResponseType | false>("/event/delete", {
    searchParams: {
      event_id: eventId,
    },
  });

  if (!res) {
    throw new NotRequestException(getEventDelete.name);
  }

  return res;
};

export const getEventList = async (
  params?: ApiRequestListOptionsType
): Promise<ApiResponseType<EventGetData[]>> => {
  const res = await getHttp().get<ApiResponseType<EventGetData[]> | false>(
    "/event/list",
    { searchParams: { ...params } }
  );

  if (!res) {
    throw new NotRequestException(getEventList.name);
  }

  return res;
};

