import { LacSelectOption } from "@/views/components/form/types";
import { EventFormFields } from "@/views/modules/CrudForms/LacEventForm/use/useFields";
import { EventAddData } from "@/kernel/api/dto/Event";

export const usePrepareData = (fields: EventFormFields) => {
  function prepareData(): EventAddData {
    const plannedDate = new Date(fields.planned_date.value);
    const split = fields.planned_time.value.split(":").map((el) => Number(el));
    plannedDate.setHours(split[0], split[1]);

    const actualDate = fields.fact_date.value
      ? new Date(fields.fact_date.value)
      : null;

    if (actualDate) {
      const splitActual = fields.fact_time.value
        .split(":")
        .map((el) => Number(el));
      actualDate.setHours(splitActual[0], splitActual[1]);
    }

    const agentId = fields.agent.value
      ? Number((fields.agent.value as LacSelectOption).value)
      : 0;
    const type = fields.type.value
      ? (fields.type.value as LacSelectOption).value
      : "";

    const res: EventAddData = {
      planned_time: plannedDate.getTime() / 1000,
      agent_id: agentId || 9,
      type,
    };
    if (actualDate) {
      res.actual_time = actualDate.getTime() / 1000;
    }

    fields.comment.value && (res.comment = fields.comment.value);

    return res;
  }

  return { prepareData };
};
