import { ReactiveVariable } from "vue/macros";
import {
  FormFieldProps,
  FormFieldSelectProps,
} from "@/views/components/form/types";
import { reactive } from "vue";
import { requiredRules } from "@/kernel/validation/vuelidate/requiredRules";
import { storeToRefs } from "pinia";
import { maxLengthRules } from "@/kernel/validation/vuelidate/maxLengthRules";
import { useStore } from "@/store";

export type EventFormFields = ReactiveVariable<{
  planned_date: FormFieldProps;
  planned_time: FormFieldProps;
  fact_date: FormFieldProps;
  fact_time: FormFieldProps;
  type: FormFieldSelectProps;
  agent: FormFieldSelectProps;
  partner: FormFieldSelectProps;
  comment: FormFieldProps;
}>;

export const useFields = () => {
  const { dictionary } = storeToRefs(useStore());

  const fields: EventFormFields = reactive({
    planned_date: {
      value: "",
      id: "planned_date",
      label: "Плановая дата",
      rules: requiredRules(),
    },
    planned_time: {
      value: "",
      id: "planned_time",
      label: "Плановое время",
      rules: requiredRules(),
    },
    fact_date: {
      value: "",
      id: "fact_date",
      label: "Фактическая дата",
      rules: requiredRules(),
    },
    fact_time: {
      value: "",
      id: "fact_time",
      label: "Фактическое время",
      rules: requiredRules(),
    },
    type: {
      value: null,
      id: "role",
      label: "Тип",
      items: Object.entries(dictionary.value.event.type).map(
        ([key, value]) => ({
          value: key,
          text: value,
        })
      ),
      rules: requiredRules(),
    },
    agent: {
      value: null,
      id: "agent",
      label: "Сотрудник",
      items: [],
      rules: requiredRules(),
    },
    partner: {
      value: null,
      id: "partner",
      label: "Контрагент",
      items: [],
      rules: requiredRules(),
    },
    comment: {
      id: "comment",
      label: "Комментарий",
      value: "",
      rules: maxLengthRules(),
    },
  });

  return {
    fields,
  };
};
