import { ref } from "vue";
import { useFormValidate } from "@/use/useFormValidate";
import { CrudFormCreateInterface } from "@/views/modules/CrudForms/CrudFormInterface";
import { useSubmitTemplate } from "@/views/modules/CrudForms/use/useSubmitTemplate";
import { useInitTemplate } from "@/views/modules/CrudForms/use/useInitTemplate";
import {
  EventFormFields,
  useFields,
} from "@/views/modules/CrudForms/LacEventForm/use/useFields";
import { useUpdate } from "@/views/modules/CrudForms/use/useUpdate";
import { usePrepareData } from "@/views/modules/CrudForms/LacEventForm/use/usePrepareData";
import { postEventAdd } from "@/kernel/api/methods/Event";
import { storeToRefs } from "pinia";
import { useStorePartner } from "@/store/partner";
import { useStoreAgent } from "@/store/agent";

export const useCreateForm: () => CrudFormCreateInterface<EventFormFields> =
  () => {
    const { setFieldRef, validateForm, resetValidations } = useFormValidate();
    const isInit = ref(false);
    const storeAgent = useStoreAgent();
    const storePartner = useStorePartner();

    const { fields } = useFields();
    const { agents } = storeToRefs(storeAgent);
    const { selectPartnerId } = storeToRefs(storePartner);

    const { prepareData } = usePrepareData(fields);
    const { submitTemplate } = useSubmitTemplate(validateForm);
    const { initTemplate } = useInitTemplate();

    const { updateAgent, updatePartner } = useUpdate(isInit);

    async function init() {
      await updatePartner(fields.partner);
      let partnerId;
      if (fields.partner.value?.value) {
        partnerId = parseInt(fields.partner.value.value);
      }
      await updateAgent(fields.agent, partnerId);

      isInit.value = true;
    }

    async function submit() {
      const payload = prepareData();
      const res = await postEventAdd(payload);

      if (res.error) {
        throw new Error(
          res.data.info
            ? JSON.stringify(res.data.info, null, 2)
            : "Не удалось добавить событие"
        );
      }
    }

    return {
      setFieldRef,
      validateForm,
      resetValidations,
      fields,
      init: initTemplate(init),
      submit: submitTemplate(submit),
      isInit,
    };
  };
